/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();
	
	&.mkdf-info-on-image {
		
		.mkdf-pli {
			
			.mkdf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

	&.mkdf-info-below-image {

		.mkdf-pli-inner {

			.mkdf-pli-text {
				@include mkdfAbsoluteHolderLayout();
				opacity: 0;
				background-color: transparent;
				box-sizing: border-box;
				@include mkdfTransition(opacity .2s cubic-bezier(0.02, 0.54, 0.58, 1));
				padding: 0;
			}

			.mkdf-pli-text-outer {
				@include mkdfRelativeHolderLayout();
				text-align: left;
			}

			.mkdf-pli-text-inner {
				@include mkdfRelativeHolderLayout();

				.mkdf-pli-add-to-cart {

					&.mkdf-default-skin {

						.button,
						.added_to_cart {
							color: #fff;
							background-color: #292932;

							&:hover {
								color: #fff;
								background-color: #292932;
							}
						}
					}

					.button,
					.added_to_cart {
						width: 100%;
						border:0;
						border-radius: 10px;
						border-top-left-radius: 0;
						border-top-right-radius: 0;
						position: absolute;
						left: 0;
						bottom: 0;
						padding:0 28px;
						box-sizing: border-box;
						line-height:50px;
						color: #fff;
						background-color: #292932;
						font-family: $default-heading-font;
						font-size: 11px;
						letter-spacing: .125em;
						font-weight: 400;
						text-transform: uppercase;
						outline: 0;

						&:after {
							font-family: 'ElegantIcons';
							content: "\e015";
							float:right;
							font-size: 16px;
						}

						&:hover {
							color: #fff;
							background-color: #292932;
						}
					}
				}
			}
		}

		.mkdf-pli-text-wrapper {
			margin: 22px 0 32px;

			.mkdf-pli-title {
				clear: both;
			}

			.mkdf-pli-category {
				float:left;
				width:50%;
				font-size: 11px;
				font-family: $default-heading-font;
				line-height:25px;
				font-weight: 400;
				text-transform: uppercase;
				letter-spacing: .125em;
				margin: 0;

				a {
					color: #938da6;

					&:hover {
						color: $first-main-color;
					}
				}
			}

			.mkdf-pli-rating-holder {
				@include mkdfRelativeHolderLayout();
				float:right;
				width: 50%;
				line-height: 25px;
				text-align: right;
				margin: 0;

				.star-rating{
					font-size: 11px;
					vertical-align: initial;
				}
			}

			.mkdf-pli-price {
				@include mkdfWooCommercePriceStyle();
				font-size: 12px;
				font-family: $default-heading-font;
				letter-spacing: .125em;
				color: #938da6;
				font-weight: 400;
				margin: 12px 0 0;

				ins, del{
					font-size: 12px;
					font-family: $default-heading-font;
					letter-spacing: .125em;
					color: #938da6;
					font-weight: 400;
					margin: 12px 0 0;
				}
			}
		}
	}

    .mkdf-pli {
        padding: 0;
        z-index: 100;

	    .mkdf-pli-title {
		    margin: 0 0 4px;
	    }
	
	    .mkdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .mkdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .mkdf-pli-rating-holder {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .mkdf-pli-rating {
		    @include mkdfWooCommerceRatingsStyle();
			font-size: 11px;
	    }

        .mkdf-pli-price {
	        @include mkdfWooCommercePriceStyle();

			del {
				color: #fff;
			}
        }
    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .mkdf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
				border-radius: 10px;
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-pli-onsale {
	            @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-pli-out-of-stock {
	            @include mkdfWooCommerceOutOfStockStyle();
            }
	
	        .mkdf-pli-new-product {
		        @include mkdfWooCommerceNewProductStyle();
	        }

			.mkdf-pli-onsale {
				+ .mkdf-pli-new-product {
					left: 100px;
				}
			}
        }
            
        .mkdf-pli-text {
			@include mkdfAbsoluteHolderLayout();
			padding: 10px;
			opacity: 0;
			background-color: rgba(42, 42, 51, 0.8);
			box-sizing: border-box;
			@include mkdfTransition(opacity .2s cubic-bezier(0.02, 0.54, 0.58, 1));
			border-radius: 10px;
        }

		.mkdf-pli-text-outer {
			@include mkdfTableLayout();
			text-align: center;
		}

		.mkdf-pli-text-inner {
			@include mkdfTableCellLayout();

            .mkdf-pli-title,
            .mkdf-pli-category a,
            .mkdf-pli-excerpt,
            .mkdf-pli-rating,
            .mkdf-pli-price {
                color: #fff;
            }

	        .mkdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .mkdf-pli-add-to-cart {

		        &.mkdf-default-skin {

			        .button,
			        .added_to_cart {
						@include mkdfButtonSolidColor();

						&:hover {
							@include mkdfButtonSolidHoverColor();
						}
			        }
		        }

		        &.mkdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }

		        &.mkdf-dark-skin {

			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            white-space: nowrap;
	            z-index: 3;
				@include mkdfButtonDefaultStyle();
				@include mkdfButtonSize();
            }

            .button.added {
                display: none;
            }
        }

	    .mkdf-pli-link {
		    @include mkdfAbsoluteHolderLayout();
	    }
    }

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();
		margin: 22px 0 0;
    }
	
	&.mkdf-product-info-light {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.mkdf-product-info-dark {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: $default-heading-color;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.mkdf-masonry-layout {
		
		.mkdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.mkdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */