/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 3px;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        .mkdf-cart-number {
            background-color: $first-main-color;
            position: relative;
            top: -5px;
            left: -10px;
            display: inline-block;
            width: 16px;
            height: 16px;
            line-height: 16px;
            font-size: 12px;
            text-align: center;
            color: #fff;
            border-radius: 50%;
        }

        &.mkdf-header-cart-icon-pack {
            
            .mkdf-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            
            .mkdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                }
            }
        }
    }

    &:hover {

        .mkdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $first-main-color;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 394px;
    background-color: #fff;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    box-shadow: $default-box-shadow;
    border-radius: 10px;
    @include mkdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 30px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 10px 0;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                border-bottom: 0;
                color: #716c80;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 90px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        vertical-align: top;
        padding-left: 20px;

        .mkdf-product-title {
            display: block;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            letter-spacing: 0;
            margin: 13px 0 0;
        }

        .remove {
            position: absolute;
            top: -6px;
            right: -7px;
            font-size: 27px;
	        line-height: 1;
            color: #7666a5;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: block;
            margin: 0;
            font-size: 11px;
            font-weight: 400;
            font-family: $default-heading-font;
            color: #938da6;
            text-transform: uppercase;
            letter-spacing: 0.125em;
            line-height: 23px;
        }

        .amount {
            display: block;
            margin: 0;
            font-size: 12px;
            font-weight: 400;
            font-family: $default-heading-font;
            color: #938da6;
            letter-spacing: 0.125em;
        }
    }

    .mkdf-cart-bottom {
        padding: 20px 0 0;
        margin: 0 -30px;

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 29px;
            padding: 20px 30px;
            border-top: 1px solid $default-border-color;
            border-bottom: 1px solid $default-border-color;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
                font-size: 16px;
                font-family: $default-heading-font;
                font-weight: 400;
            }

            .mkdf-total-amount {
                text-align: right;
	            font-size: 16px;
            }
        }

        .mkdf-btn-holder {
            padding: 0px 30px;
            text-align: center;
            width: 100%;

            .mkdf-view-cart {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                height: 40px;
                line-height: 40px;
                padding: 0 30px;
                font-size: 11px;
                font-family: $default-heading-font;
                letter-spacing: 0.125em;
                font-weight: 500;
                border-radius: 3em;
                color: #fff;
                background-color: $first-main-color;
                text-align: center;
                box-sizing: border-box;
                @include mkdfTransition(color .2s cubic-bezier(0.02, 0.54, 0.58, 1), background-color .2s cubic-bezier(0.02, 0.54, 0.58, 1));

                span {
                    display: block;
                }

                &:hover {
                    color: #fff;
                    background-color: $default-heading-color;
                }
            }
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */