/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: $additional-background-color;
            border: 0;
            text-align: center;
            box-sizing: border-box;
        }

        .mkdf-quantity-input {
            position: relative;
            height: 48px;
            width: 46px;
            padding: 0;
            margin: 0 22px 0 0;
            font-family: inherit;
            line-height: 48px;
            outline: 0;
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 24px;
            height: 24px;
            line-height: 24px;
            border-left: 0;
            cursor: pointer;
            font-size: 20px;
            text-align: left;
            @include mkdfTransition(color .15s cubic-bezier(0.02, 0.54, 0.58, 1));

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity-plus {
            top: 0;
            border-bottom: 0;
            border-top-right-radius: 10px;
        }

        .mkdf-quantity-minus {
            bottom: 0;
            border-bottom-right-radius: 10px;
        }
    }
}

abbr.required {
    border: none !important;
    text-decoration: none !important;
}

body .select2-container--default.select2-container--open .select2-dropdown{
    top: -20px;
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    #billing_country_field .select2-container .selection .select2-selection--single,
    textarea {
        @extend %input-style;
    }

    #billing_country_field .select2-container .selection .select2-selection--single .select2-selection__rendered{
        padding: 0;
    }

    #billing_country_field .select2-container--default .select2-selection--single .select2-selection__arrow{
        top: calc(50% - 12px);
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */