/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
						border-radius: 10px;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
		padding: 5.8% 0 0 21px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 0 0 14px;
            font-size: 32px;
            font-weight: 400;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
            margin-top:21px;
            color: $default-text-color;
			font-size: 22px;
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 23px 0 3px;
            
            .woocommerce-review-link{
                margin-left: 14px;
            }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 18px 0 0;
		
		    p {
                line-height: 26px;
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 17px 0 0;
            color: $default-heading-color;
            font-weight: 400;
            font-family: $default-heading-font;
            font-size: 11px;
            text-transform: uppercase;
            letter-spacing: 0.125em;
		
		    > span {
			    display: block;
			    margin: 3px 0;
                line-height: 26px;
			
			    a, .sku {
				    color: #999;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 40px 0 0;
            
            button.single_add_to_cart_button{
                height: 48px;
                margin-left: 11px;
            }
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 28px 0 0;
	    }
        
        .mkdf-single-product-meta-title{
            margin-top:45px;
            font-size: 22px;
            line-height: 22px;
            color: $default-heading-color;
            font-weight: 400;
            font-family: $default-heading-font;
        }
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 62px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			clear: both;
            
            > li.active {
                
                a{
                    background-color: $first-main-color;
                    color: #fff;
                }
                
            }
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
                margin: 0 21px 5px 0;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 6px 26px;
                    background-color: $additional-background-color;
                    border-radius: 3em;
                    text-transform: uppercase;
                    font-size: 11px;
                    font-family: $default-heading-font;
                    line-height: inherit;
                    color: #938da6;
                    font-weight: 400;
                    letter-spacing: .125em;
                    @include mkdfTransition(color .2s cubic-bezier(0.02, 0.54, 0.58, 1), background-color .2s cubic-bezier(0.02, 0.54, 0.58, 1));
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 23px 0 20px;
			}
		}
		
		table {
			margin: 21px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				font-family: $default-heading-font;
				font-weight: 400;
				text-transform: uppercase;
				letter-spacing: .125em;
				font-size: 11px;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				color: $default-text-color;
				width: 99%;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 17px 0 20px;
			
			h2 {
				font-size: 22px;
				line-height: 1.45em;
				font-weight: 400;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
                
                .comment-form-rating{
                    margin:20px 0;
                }
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;
		
		> h2 {
			display: block;
			margin: 60px 0 25px;
            font-size: 22px;
          	font-weight: 400;
			text-transform: capitalize;
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */