/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include mkdfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include mkdfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include mkdfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .mkdf-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .mkdf-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #e5e5e5;
                margin: 30px 0 7px;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -4px;
                    height: 10px;
                    width: 10px;
                    background-color: $first-main-color;
                    border-radius: 50%;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include mkdfTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 33px 0 0;
            height:40px;
            line-height: 40px;
            
            .button {
                float: left;
            }
            
            .price_label {
                float: right;
                color: $first-main-color;
                font-size: 11px;
                font-family: $default-heading-font;
                font-weight: 400;
                text-transform: uppercase;
                letter-spacing: .125em;
            }
        }
    }
    
    &.widget_product_categories {
        
        ul {
            margin-top: 20px;
            
            li {
                margin: 0;
                color: $default-heading-color;
                font-size: 11px;
                font-family: $default-heading-font;
                font-weight: 400;
                text-transform: uppercase;
                letter-spacing: .125em;
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .mkdf-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 110px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                }
                
                .amount {
                    color: $default-heading-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: Poppins,sans-serif;
                font-size: 11px !important;
                line-height: 2em;
                letter-spacing: .125em;
                border-radius: 3em;
                font-weight: 500;
                text-transform: uppercase;
                outline: 0;
                box-sizing: border-box;
                padding: 8px 28px;
                color: #938da6;
                 background-color: #f5f9ff;
                border: 0;
                white-space: nowrap;
                cursor: pointer;
                margin: 0 6px 10px 0;
                @include mkdfTransition(color .2s cubic-bezier(0.02, 0.54, 0.58, 1), background-color .2s cubic-bezier(0.02, 0.54, 0.58, 1));
            }

            a:hover{
              color: #fff;
              background-color: $first-main-color;
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include mkdfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include mkdfRelativeHolderLayout();
                border: 0;
                box-sizing: border-box;
                background-color: #7bb9ff;
                border-radius:25px;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 50px;
                line-height:50px;
                color: #fff;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include mkdfTransition(color .2s cubic-bezier(0.02, 0.54, 0.58, 1));
            }
            
            input {
                width: calc(100% - 75px);
                padding: 10px 10px 10px 30px;
                font-size: 11px;
                font-family: $default-heading-font;
                font-weight: 500;
                text-transform: uppercase;
                letter-spacing: .125em;
                box-sizing: border-box;
    
                @include mkdfPlaceholder {
                    color: #fff;
                }
            }
            
            button {
                width: 75px;
                font-size: 14px;
                cursor: pointer;
                
                &:hover {
                    color: $default-heading-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */